<template>
  <div class="table" v-if="tableData">
    <table-user class="table-section"
                v-if="blockData.user"
                :data="blockData.user"
    ></table-user>
    <table-block-one class="table-section"
                     v-if="blockData.block_1"
                     :data="blockData.block_1"
                     :common="blockOne"
    />
    <table-block-two class="table-section"
                     v-if="blockData.block_2"
                     :data="blockData.block_2"
                     :common="blockTwo"
    />
    <table-block-three class="table-section"
                       v-if="blockData.block_3"
                       :data="blockData.block_3"
                       :common="blockThree"
    />
    <table-bonus class="table-section"
                 :data="blockData.bonus"
    />
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';
  import TableUser from "./tableSections/TableUser";
  import TableBlockOne from "./tableSections/TableBlockOne";
  import TableBlockTwo from "./tableSections/TableBlockTwo";
  import TableBlockThree from "./tableSections/TableBlockThree";
  import TableBonus from "./tableSections/TableBonus";

  export default {
    components: {
      TableUser,
      TableBlockOne,
      TableBlockTwo,
      TableBlockThree,
      TableBonus
    },
    data() {
      return {
        tableData: null,
        blockData: null
      }
    },
    mounted() {
      this.tableData = {...this.TABLE_DATA}
      this.getBlocks();
      this.roundValue(this.blockData);
    },
    computed: {
      ...mapGetters(['TABLE_DATA']),
      blockOne() {
        return this.tableData.block_1_ye.toFixed(2);
      },
      blockTwo() {
        return this.tableData.block_2_ye.toFixed(2)
      },
      blockThree() {
        return this.tableData.block_3_ye.toFixed(2)
      }

    },
    methods: {
      getBlocks() {
        this.blockData = {...this.tableData.data}
        delete this.tableData.data;
        this.blockData.user = {...this.tableData}
      },

      roundValue(data) {
        console.log(data);
        function ceiling(obj) {
          Object.entries(obj).forEach(([key, val]) => {
            if (key !== 'user_id') {
              val = Math.ceil(val * 100) / 100
              obj[key] = val;
            }
          })
        }

        Object.values(data).forEach(item => {
          if (!(item instanceof Array) && item) {
            ceiling(item);
          } else if (item instanceof Object && item) {
            item.forEach(obj => {
              ceiling(obj)
            })
          }
        })
      }
    }
  }
</script>

