import Vue from 'vue'
import axios from 'vue-axios'
import VueAxios from "vue-axios";
import Vuex from 'vuex'

Vue.use(VueAxios, axios)

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: null,
    cacheCard: null,
    cacheParent: null,
    tooltipComponent: null,
    tooltipIsShow: false,
    userCounter: 1,
    tableData: null,
    modalIsOpen: false,
    loading: false
  },
  mutations: {
    ADD_CACHE_PARENT(state, parent) {
      state.cacheParent = parent;
    },
    ADD_CARD_TO_CACHE(state, card) {
      state.tooltipIsShow = true;
      card.tooltip.isOpen = true;
      state.cacheCard = card;
    },
    CLOSE_TOOLTIP(state) {
      state.tooltipIsShow = false
      if (state.cacheCard) {
        state.cacheCard.tooltip.isOpen = false;
      }
    },
    GET_USER(state) {
      Vue.set(state, "user", {
        root: true,
        id: `Vasya-${state.userCounter}`,
        firstName: "Vasya",
        lastName: "Pupkin",
        groupVolume: 0,
        countOfChildren: 8,
        l_amount: 70,
        list: [],
        tooltip: {
          isOpen: false,
          info: 'TooltipInfo',
          removing: 'TooltipRemove'
        }
      });
    },
    ADD_CARD(state, [card, data]) {
      card.list.push(data);
    },
    OPEN_MODAL(state) {
      state.modalIsOpen = !state.modalIsOpen;
    },
    CLOSE_MODAL(state) {
      state.modalIsOpen = false;
    },
    SET_TABLE_DATA(state, data) {
      state.tableData = Object.assign({}, data)
    },
    DELETE_TABLE_DATA(state) {
      state.tableData = null
    },
    LOADING(state, visible) {
      state.loading = visible;
    }
  },
  actions: {
    openModal({commit, state}) {
      document.body.style.overflow = 'hidden';
      commit('LOADING', true);
      let data = JSON.stringify(state.user);

      fetch("./data.php", {
        method: "POST",
        body: data
      }).then(response => {
        if (response.ok) {
          return response.json()
        }
      }).then(data => {
        commit('SET_TABLE_DATA', data)
        return data;
      }).then(() => {
        commit('LOADING', false);
        commit('OPEN_MODAL');
      })
    },
    closeModal({commit}) {
      commit('CLOSE_MODAL');
      commit('DELETE_TABLE_DATA');
      document.body.style.overflow = '';
    },
    closeTooltipCache({commit}) {
      commit('CLOSE_TOOLTIP');
    },
    openTooltip({commit}, card) {
      commit('CLOSE_TOOLTIP');
      commit('ADD_CARD_TO_CACHE', card)
    },
    add_parent_card({commit}, parent) {
      commit('ADD_CACHE_PARENT', parent)
    },
    add_card({commit, state}, card) {
      let parentCard = Object.assign({}, state.cacheParent)
      if (!card) commit('GET_USER');
      if (card) {
        state.userCounter++;
        let data = {
          id: `partner-${state.userCounter}`,
          firstName: 'partner-',
          lastName: `${state.userCounter}`,
          groupVolume: 0,
          countOfChildren: 8,
          l_amount: 70,
          list: [],
          tooltip: {
            isOpen: false,
            info: 'TooltipInfo',
            removing: 'TooltipRemove'
          },
          // parentCard
        }
        commit('ADD_CARD', [card, data]);
      }
      console.log(parentCard)
    },
  },
  getters: {
    tooltip_is_show: state => state.tooltipIsShow,
    GET_ROOT_USER: state => state.user,
    TABLE_DATA: state => state.tableData,
    MODAL_IS_OPEN: state => state.modalIsOpen,
    TOOLTIP_IS_OPEN: state => state?.cacheCard.tooltip.isOpen,
    IS_LOADING: state => state.loading
  }
})
